<template>
  <FormsContainer>
    <VTypography font-weight="heavy" font-style="header-1" class="mb-5"
      ></VTypography
    >

    <TypographyWithInfo font-weight="medium" class="mb-1">
         ,     

      <template v-slot:popover>
        <PopoverContent :width="363">
          <VTypography font-weight="medium"
            >    .</VTypography
          >
        </PopoverContent>
      </template>
    </TypographyWithInfo>

    <DatePicker v-model="releaseDate" class="mb-5" />

    <TypographyWithInfo font-weight="medium" class="mb-1">
         

      <template v-slot:popover>
        <PopoverContent :width="440">
          <VTypography font-weight="medium"
            >     
            .</VTypography
          >
        </PopoverContent>
      </template>
    </TypographyWithInfo>

    <DatePicker
      v-model="developmentDate"
      label="   "
    />
  </FormsContainer>
</template>

<script>
import { mapGetters, mapMutations } from "vuex";

import VTypography from "@/components/ui/Typography";
import DatePicker from "@/components/date-picker/DatePicker";
import FormsContainer from "@/components/forms/FormsContainer";
import PopoverContent from "@/components/popover/PopoverContent";
import TypographyWithInfo from "@/components/ui/TypographyWithInfo";

export default {
  name: "DatesForm",
  components: {
    DatePicker,
    TypographyWithInfo,
    PopoverContent,
    VTypography,
    FormsContainer,
  },
  computed: {
    ...mapGetters("dates", ["getStartReleaseDate", "getStartDevelopmentDate"]),
    releaseDate: {
      get() {
        return this.getStartReleaseDate;
      },
      set(date) {
        this.changeReleaseDate(date);
      },
    },
    developmentDate: {
      get() {
        return this.getStartDevelopmentDate;
      },
      set(date) {
        this.changeDevelopmentDate(date);
      },
    },
  },
  methods: {
    ...mapMutations("dates", ["changeReleaseDate", "changeDevelopmentDate"]),
  },
};
</script>
