            info-text="-   "
          />

          <TextInput
            input-type="contrast"
            :maxlength="100"
            :error="errors.workplace.english"
            v-model="author.workplace.english"
            name="author.workplace.english"
            placeholder=" "
            root-class="mb-5"
          />

          <TextInput
            input-type="contrast"
            :maxlength="100"
